import React from 'react'
import Link from 'gatsby-link'
import DSLeftNav from '../components/LeftNav'

const Typography = () => (
  <div className="ds-app-grid">
    <DSLeftNav />

    <div className="ds-main-content">
      <div className="ds-container">
        <h1>Typography</h1>
      </div>
    </div>
  </div>
)

export default Typography



